  width: 40px;
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
  border: 1px solid #ebebeb;
  background: #ffffff;
  border-top-right-radius: 0;
  padding: 10px 10px;
  height: 42px;
}

.dd .edit:before {
  content: '\e05f';
  display: block;
  position: absolute;
  left: 0;
  top: 10px;
