  display: flex;
  flex-wrap: wrap;
  border-top: 0;
  min-height: calc(100vh - 180px);
}

#filemanager .flex #left {
  flex: 4;
  position: relative;
  min-height: 230px;
}

#filemanager .flex #left #no_files {
  display: none;
}

#filemanager .flex #left #no_files h3 {
  text-align: center;
  margin-top: 55px;
  margin-bottom: 75px;
  color: #949494;
}

#filemanager .flex #right {
  flex: 1;
}

#filemanager #right .right_details {
  display: block;
}

#filemanager #right .right_none_selected {
  display: none;
  text-align: center;
}

#filemanager #right .right_none_selected i {
  width: 100%;
  text-align: center;
  font-size: 30px;
  margin-left: 0;
  padding: 50px;
  display: block;
  background: #f9f9f9;
}

#filemanager #right .right_none_selected p {
  text-align: center;
  color: #bbbbbb;
  padding: 10px;
  border-bottom: 1px solid #f1f1f1;
}

#filemanager #files {
  display: flex;
  list-style: none;
  width: 100%;
  margin: 0;
  flex-wrap: wrap;
  padding: 10px;
  position: relative;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Chrome/Safari/Opera */
  -khtml-user-select: none;
  /* Konqueror */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
}

#filemanager #files li {
  flex: 1;
  width: 100%;
  min-width: 200px;
  max-width: 250px;
}

#filemanager #files li .file_link {
  padding: 10px;
  margin: 10px;
  cursor: pointer;
  border-radius: 3px;
  border: 1px solid #ecf0f1;
  overflow: hidden;
  background: #f6f8f9;
  display: flex;
  height: 70px;
}

#filemanager #files li .file_link .details {
  flex: 2;
  overflow: hidden;
  width: 100%;
}

#filemanager #files li .file_link .details small {
  font-size: 11px;
  position: relative;
  top: -3px;
  font-weight: 300;
}

#filemanager #files li .file_link .link_icon {
  flex: 1;
}

#filemanager #files li .file_link img,
#filemanager #files li .file_link .img_icon {
  display: none;
}

#filemanager #files li .file_link.image img,
#filemanager #files li .file_link.image .img_icon {
  display: block;
}

#filemanager #files li .file_link.image img {
  height: 50px;
}

#filemanager #files li .file_link.image .img_icon {
  width: 50px;
  height: 50px;
  display: block;
}

#filemanager #files li .file_link {
  transition: all 340ms;
}

#filemanager #files li .file_link:hover,
#filemanager #files li .file_link.selected {
  background: #868e96 !important;
  border-color: #adb5bd;
  color: #ffffff;
}

#filemanager #files li .file_link:hover h4,
#filemanager #files li .file_link.selected h4 {
  color: #ffffff;
}

#filemanager #files li .details h4 {
  margin-bottom: 2px;
  margin-top: 10px;
  max-height: 17px;
  height: 17px;
  overflow: hidden;
  font-size: 14px;
  text-overflow: ellipsis;
}

#filemanager #files li .details.folder h4 {
  margin-top: 16px;
}

#filemanager .file_link.folder i.icon {
  float: left;
  margin-left: 10px;
}

#filemanager .file_link.folder .num_items {
  display: block;
}

#filemanager .file_link .link_icon {
  text-align: center;
  padding-left: 0;
  margin-left: 0;
  margin-right: 5px;
}

#filemanager .file_link .link_icon i {
  padding-left: 0;
  padding-right: 0;
  position: relative;
  top: 5px;
}

#filemanager .file_link i.icon:before {
  font-size: 40px;
}

#filemanager .detail_img {
  border-bottom: 1px solid #f1f1f1;
  background: #eeeeee;
}

#filemanager .detail_img img {
  width: 100%;
  height: auto;
  display: inline-block;
}

#filemanager .detail_img i {
  display: block;
  width: 100%;
  text-align: center;
  font-size: 70px;
  margin-left: 0;
  padding: 30px;
  background: #f9f9f9;
}

#filemanager .detail_img.folder i.fa-folder {
  display: block;
}

#filemanager .detail_img.file i.fa-file {
  display: block;
}

#filemanager .detail_img.image img {
  display: block;
}

#filemanager .detail_info {
  padding: 10px;
}

#filemanager .detail_info .selected_file_count,
#filemanager .detail_info.folder .selected_file_size {
  display: none;
}

#filemanager .detail_info.folder .selected_file_count {
  display: block;
}

#filemanager .detail_info span {
  display: block;
  clear: both;
}

#filemanager .detail_info a {
  color: #1c7ed6;
}

#filemanager .detail_info .selected_file_count,
#filemanager .detail_info .selected_file_size {
  padding-top: 0;
}

#filemanager .detail_info h4 {
  float: left;
  color: #bbbbbb;
  font-size: 13px;
  margin: 3px 8px 0 0;
  padding-bottom: 2px;
  font-weight: 300;
}

#filemanager .detail_info p {
  float: left;
  color: #444444;
  padding-bottom: 3px;
  font-size: 13px;
  font-weight: 400;
}

#filemanager #uploadProgress {
  display: none;
  background: #eeeeee;
}

#filemanager #file_loader {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(255, 255, 255, 0.7);
  z-index: 9;
  text-align: center;
}

#filemanager #file_loader #file_loader_inner {
  width: 60px;
  height: 60px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -30px;
  margin-top: -30px;
}

#filemanager #file_loader img {
  width: 80px;
  height: 80px;
  margin-top: 50px;
  opacity: 0.5;
  -webkit-animation: spin 1.2s ease-in-out infinite;
  -moz-animation: spin 1.2s ease-in-out infinite;
  animation: spin 1.2s ease-in-out infinite;
}

#filemanager #file_loader p {
  margin-top: 40px;
  position: absolute;
  text-align: center;
  width: 100%;
  top: 50%;
  font-weight: 400;
  font-size: 12px;
}

@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
  }
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}




