.turbolinks-progress-bar {
  height: 2px;
  background-color: $brand-info;
}

#content {
  animation: blurin 340ms ease-in;
}

@keyframes blurin {
  0% {
    opacity: 0.25;
    filter: blur(16px);
  }
  25% {
    opacity: 0.5;
    filter: blur(8px);
  }
  50% {
    opacity: 0.75;
    filter: blur(4px);
  }
  100% {
    opacity: 1;
    filter: blur(0px);
  }
}



